.container {
    background-color: rgb(248, 249, 249);
    width: 200px;
    height: 670px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 5px;
    border-radius: 10px;
    padding: 20px;
    margin-left: 30px;
    margin-top: 145px;
  }
  
  .container img {
    width: 400px;
    margin-top: 120px;
  }
  
  .container label {
    color: black;
    font-family: Helvetica;
    margin-left: 8px;
    font-size: 13px;
  }
  